import React from 'react'

import { Link } from 'gatsby'
import moment from 'moment'

import classNames from 'classnames'
import styles from './Blog.module.scss'

const Blog = ({ blogData }) => {
  return (
    <Link className={styles.link} to={blogData.path}>
    <div className={styles.blog} key={blogData.path}>
      <div className={styles.top}>
        <span className={styles.postedAt}>{moment(blogData.date).format('LL')}</span>
        <span className={styles.timeToRead}>{blogData.timeToRead} min read</span>
      </div>
      <h2 className={styles.postTitle}>{blogData.title}</h2>
      {
        blogData.tags && blogData.tags.length ? (
          <div className={styles.tags}>
            {
              blogData.tags.map(tag => (
                <Link className={classNames([styles.link, styles.tag])} key={tag} to={`/tags/${tag}`}>
                  #{tag}
                </Link>
              ))
            }
          </div>
        ) : null
      }
    </div>
    </Link>
  )
}

export default Blog